.account-navigation-container{position:absolute;top:0;right:0;width:100%;height:100vh;background-color:#fff;z-index:1999}.account-navigation-container .anc-row1{position:relative;left:0px;top:0px;padding:0px 15px;border:none;text-align:right;font-size:30px;color:var(--black-101);cursor:pointer;z-index:10}.account-navigation-container .anc-row2{text-align:center;padding:30px 0}.account-navigation-container .anc-row2 img{width:188px}.account-navigation-container .anc-row3 ul{padding:0;margin:0}.account-navigation-container .anc-row3 ul li{display:flex;justify-content:space-between;padding:11px 16px;border-bottom:1px solid var(--gray-200);font-size:16px;font-weight:500}.account-navigation-container .anc-row3 ul li a{color:var(--black-101);width:100%}.account-navigation-container .anc-row3 ul li .notification-btn{display:flex;align-items:center;gap:10px}.account-navigation-container .anc-row3 ul li .notification-btn .notification-counter{font-size:13px;text-align:center;align-content:center;width:21px;height:21px;border-radius:50%;background-color:red;color:#fff}.account-navigation-container .anc-row3 ul li:last-child{border:none}.account-navigation-container .anc-row3 ul li:after{content:"";font-family:FontAwesome}.account-navigation-container .anc-row3 ul li:last-child:after{border:none;content:""}